/** UI **/
.header {
  background-color: #FFFFFF;
  border: 1px solid #d9d9d9;
  padding: 1rem;
}

.header a {
  color: inherit;
  text-decoration: none;
}

.header h2 {
  margin: 0;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;
  padding: 2.5rem;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 4rem;
}
.footer p {
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .footer {
    height: 8rem;
  }
  .footer p {
    font-size: 2rem;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
}
.dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 1rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  display: none;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #333333;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/** menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  display: block;
  opacity: 1;
}

.contenido-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10rem;
}
.contenido-main h1 {
  font-size: 2.5rem;
  margin: 0;
}

.enlace {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
