  padding: 1rem 10rem;
  margin-top: 2rem;
  margin-bottom: 5rem;
  border-radius: 1rem;
  color: #333333;
}
.enlace:hover {
  background-color: gray;
  color: #FFFFFF;
}

.enlace span {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: #333333;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 1.2rem;
  flex-shrink: 0;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  padding: 1rem;
}

.listas {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 80rem;
  width: 100%;
}

.lista {
  background-color: #FFFFFF;
  border: 1px solid #d9d9d9;
  border-radius: 1rem;
  padding: 2rem;
  line-height: 3.5rem;
  margin-bottom: 1rem;
  width: 40rem;
  max-width: 80rem;
}

.lista span {
  background-color: #ec0c0c;
  border-radius: 1rem;
  padding: 0.5rem 1rem;
  color: #FFFFFF;
  font-size: 1.3rem;
}

.enlace-lista {
  display: block;
  color: inherit;
  text-decoration: none;
}

.main-productos {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10rem;
}

.main-productos form {
  display: flex;
  flex-direction: column;
}

.main-productos .nombre {
  padding: 1.5rem;
  border: 1px solid #d9d9d9;
  border-radius: 0.8rem;
  background-color: white;
  width: 30rem;
  max-width: 40rem;
  margin-bottom: 5rem;
}

.main-productos form button {
  padding: 2rem;
  background-color: #E08709;
  border: none;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 0.5rem;
  color: #FFFFFF;
