  border: 1px solid #d9d9d9;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 200px;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

/** Paginas Internas **/
.alerta {
  padding: 1rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #ec0c0c;
}
.alerta.exito {
  background-color: #71B100;
}

.lista {
  margin: 4rem auto 0 auto;
  width: 100%;
  border-spacing: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.lista .izquierda {
  text-align: left;
}
.lista .imagen-tabla {
  width: 8rem;
  margin: 0 auto;
}
.lista .boton-rojo-block {
  margin-top: 0;
}
.lista .boton-amarillo-block {
  margin-top: 1rem;
}

.imagen-small {
  width: 20rem;
}